img {
    -webkit-user-drag: none;
    -moz-user-drag: none;
    user-drag: none;    
}

.robot {
    max-width:140px;
    height:auto;
}

.unselect {
    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
}

.arrow {
    cursor: pointer;
    -webkit-animation: eyecatch .4s infinite;
    animation: eyecatch .4s infinite;
    opacity: .65;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.arrow:hover {
    opacity: 1;
    -webkit-animation-play-state: paused;
     animation-play-state: paused;
}

@-webkit-keyframes eyecatch {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    50% {
        -webkit-transform: scale(.9);
                transform: scale(.9);
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes eyecatch {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    50% {
        -webkit-transform: scale(.9);
                transform: scale(.9);
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
